@import "./../../../../vars.scss";

.item {
    display: flex;
    align-items: center;
    backdrop-filter: blur(3px);
    &__text {
        padding: 0 10px;
    }
    &__button-orange {
        color: white;
        text-decoration: none;
        padding: 0.8em 1em calc(0.8em + 3px);
        border-radius: 3px;
        background: #ff5126;
        box-shadow: 0 -3px #ce3c18 inset;
        transition: 0.2s;
        cursor: pointer;
    }
    &__button-blue {
        color: white;
        text-decoration: none;
        padding: 0.8em 1em calc(0.8em + 3px);
        border-radius: 3px;
        background: #292b6c;
        box-shadow: 0 -3px #17193f inset;
        transition: 0.2s;
        cursor: pointer;
    }

    @media (max-width: $tablet) {
        backdrop-filter: blur(3px);
        height: 50px;
        display: block;
        padding-bottom: 80px;
    }
}
