.theme {
    padding: 0;
    display: flex;
    display: inline-block;
    &__orange {
        color: #ff5126;
        cursor: pointer;
        text-decoration: underline;
        display: inline-block;
    }
    &__blue {
        color: #292b6c;
        cursor: pointer;
        text-decoration: underline;
        display: inline-block;
    }
    &__white {
        color: white;
        text-decoration: underline;
    }
}
