@import "./../../vars.scss";

.admin {
    background: url(../../assets/Images/HotImages/mountsBig.png) no-repeat;
    background-size: cover;
    min-height: 700px;
    width: 100%;
    &__block {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 10px 0;
    }
    &__title-orange {
        background-color: white;
        opacity: 0.6;
        text-transform: uppercase;
        font-size: 25px;
        padding: 5px;
        border: 3px solid #ff5126;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    &__title-blue {
        background-color: white;
        opacity: 0.6;
        text-transform: uppercase;
        font-size: 25px;
        padding: 5px;
        border: 3px solid #292b6c;
        margin-top: 20px;
        margin-bottom: 20px;
    }
}
.wrapper-add {
    margin-bottom: 20px;
}
