.demoForm {
	width: 500px;
	margin: auto;
}
.loginError{
	text-align: center;
    display: none;
	color:grey
}
.login{
	&__form{
		margin-bottom: 20px;
	}
	&__button{
		position: relative;
		left: 50%;
		transform: translate(-50%, 0);
	}
}
.form-control {
    display: block;
    width: 100%;
    height: 34px;
    font-size: 14px;
    line-height: 1.42857;
    color: rgb(85, 85, 85);
    background-color: rgb(255, 255, 255);
    background-image: none;
    box-shadow: rgba(0, 0, 0, 0.075) 0px 1px 1px inset;
    padding: 6px 12px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(204, 204, 204);
    border-image: initial;
    border-radius: 4px;
    transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
}