@import "./../../vars.scss";

.button {
  padding: 10px 30px 13px;
  background: none;
  border: 2px solid #fff;
  color: #fff;
  font-size: 16px;
  font-family: "Open Sans";
  font-weight: 400;
  margin-bottom: 30px;
  cursor: pointer;
  &__welcome {
    background-color: transparent;
    margin-bottom: 20%;
  }
  &__orange {
    background: #ff5126;
    margin: 0 auto;
    border: none;
    cursor: pointer;
    &__pos {
      background: #ff5126;
      margin: 0 auto;
      border: none;
      margin-bottom: 30px;
      font-size: 20px;
    }
  }
  &__blue {
    background: #292b6c;
    margin: 0 auto;
    border: none;
    cursor: pointer;
    &__pos {
      background: #292b6c;
      margin: 0 auto;
      border: none;
      margin-bottom: 30px;
      font-size: 20px;
    }
  }

  &:disabled {
    background-color: lightgray;
  }

  @media (max-width: $tablet) {
    &_orangeForm {
      background: #ff5126;
      margin: 0 auto;
      border: none;
      max-width: 360px;
      width: 100%;
    }
  }
}
