@import "./../../vars.scss";

.wrapper-directions {
	display: flex;
	flex-direction: column;
	align-items: center;

	&__background {
		background: url(../../assets/Images/HotImages/mountsBig.png) no-repeat;
		background-size: cover;
		min-height: 500px;
		width: 100%;
		background-position: inherit;
	}
	&__title {
		margin: 0 auto;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	&__text {
		font-size: 16px;
		font-family: "Open Sans", sans-serif;
		font-weight: 400;
		line-height: 2;
		color: #111111;
		@media (max-width: $phone) {
			font-size: 10px;
		}
	}
	&__texti {
		padding: 10px;
		font-size: 20px;
		border: 1mm solid #ff5126;
		margin-bottom: 20px;
		@media (max-width: $netbook) {
			font-size: 16px;
		}
	}
	&__cards {
		width: 1064px;
		display: flex;
		background-size: cover;
		justify-content: space-between;
		flex-wrap: wrap;
		align-content: space-between;
		padding: 35px;
		background-color: #ffffff;
		margin-top: -200px;
		@media (max-width: $desktop) {
			width: 1000px;
			padding: 10px;
		}
		@media (max-width: $netbook) {
			width: 500px;
			padding: 10px;
		}
		@media (max-width: $phone) {
			width: 350px;
			padding: 10px;
			margin-top: -400px;
		}
	}
	&__orange {
		color: white;
		background: #ff5126;
		margin: 0 auto;
		border: 1rem solid;
	}
	&_blue {
		color: white;
		background: #292b6c;
		margin: 0 auto;
		border: 1rem solid;
	}
}
.wrapper-directions__title{
	padding-bottom: 8px;
}