@import "./../../../../vars.scss";

.directions-cards-wrap {
	width: 482px;
	border: solid 1px #d9d9d9;
	margin-bottom: 10px;

	&__img {
		width: 100%;
		height: 300px;
	}

	&__title {
		font-family: Roboto, sans-serif;
		font-weight: 300;
		font-size: 20px;
		text-transform: capitalize;
		line-height: 1.2;
		color: #454545;
		padding: 30px 20px 20px;
		text-align: left;	
	}

	&__text {
		font-family: "Open Sans", sans-serif;
		font-weight: 400;
		line-height: 2;
		font-size: 16px;
		color: #454545;
		font-style: italic;
		word-wrap: break-word;
		text-align: left;
	}
}
