@import "./../../vars.scss";

.header {
  position: fixed;
  display: flex;
  z-index: 10;
  width: 100%;
  justify-content: space-between;
  background-color: #1a1a1a;
  color: #ff5126;
    @media (max-width: $phone) {
    display: block;
  }
  &__title {
    margin: 20px;
    display: inline-block;
    font-size: 20px;
    font-weight: 300;
    text-align: right;
    cursor: pointer;
    @media (max-width: $phone) {
    display: block;
      margin: 5px;
      font-size: 14px;
      text-align: center;
    }
  }
  &__title-admin {
    text-transform: none;
  }
}
.header__title:hover {
  text-shadow: 1px 0px 10px white;
  transition: 1s linear 0s;
  outline: 0 none;
}
